/* General Styling */
h2 {
  width: 100%;
  margin: 0px;
  text-align: center;
  text-decoration: underline;
  font-family: "Josefin Sans";
  font-weight: 700;
  color: rgb(51, 51, 51);
}

h4 {
  margin: 0;
  font-weight: 400;
  padding-bottom: 10px;
  font-family: "Josefin Sans";
  font-size: 1em;
  color: rgb(63, 63, 63);
}

hr {
  color: white;
  width: 100%;
}

.header {
  display: none;
}

.title-wrapper {
  text-align: left;
  width: 100%;
  margin-bottom: 3vh;
}

.header-animation {
  -webkit-animation: header-fade 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: header-fade 2s;
}

@-webkit-keyframes header-fade {
  from {
    opacity: 0;
    -webkit-transform: translate(0px, 60px);
            transform: translate(0px, 60px);
  }
  to {
    opacity: 1;
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
}

@keyframes header-fade {
  from {
    opacity: 0;
    -webkit-transform: translate(0px, 60px);
            transform: translate(0px, 60px);
  }
  to {
    opacity: 1;
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
}

.content-animation {
  -webkit-animation: content-fade 1.5s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: content-fade 1.5s;
}

@-webkit-keyframes content-fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes content-fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

