.content-block {
  width: 90%;
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-bottom: 3vh;

  h3 {
    margin: 0;
    padding-bottom: 5px;
    padding-top: 1vh;
    font-family: "Josefin Sans";
    font-weight: 600;
    font-size: 1.2em;
  }
  
  h5 {
    margin: 0;
    font-family: "Josefin Sans";
    font-weight: 400;
    font-style: italic;
    font-size: 12px;
    color: rgb(240, 106, 44);
  }

  .content-img-wrapper,
  .info {
    padding: 1vh 2vw 1vh 2vw;
  }

  .content-img {
    width: 100%;
    border-radius: 10px;
    margin: 0px;
    padding: 0px;
    transition: 0.5s;
  }

  .content-img:hover {
    transform: translate(0px, -10px);
    transition: 0.5s;
    box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.7);
  }

  .write-up {
    margin-top: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.3em;
    font-family: "Raleway";
    text-align: justify;
  }
}

@media only screen and (min-width: 768px) {
  .content-block {
    flex-direction: row;
    h3 {
      font-size: 1.3em;
    }

    h4 {
      font-size: 1em;
    }

    h5 {
      font-size: 12px;
    }

    .content-img-wrapper,
    .info {
      padding: 2vh 2vw 1vh 1vw;
    }

    .content-img-wrapper {
      display: flex;
      align-items: center;
    }

    .content-img {
      width: 35vw;
    }

    .write-up {
      font-size: 15px;
    }
  }
}

@media only screen and (min-width: 1000px) {
  .content-block {
    width: 60%;
    display: inline-flex;
    text-align: left;

    h3 {
      font-size: 1.5em;
    }

    h5 {
      font-size: 15px;
    }
    .content-img {
      width: 20vw;
      border-radius: 10px;
    }

    .content-img-wrapper,
    .info {
      padding: 3vh 1vw 3vh 1vw;
    }
  }
}
