.seperator {
  padding: 0px;
  margin: 0px;
  height: 2px;
  text-align: center;
  width: 50vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: rgb(226, 226, 226);

  .seperator-diamond-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 40%;
    background-color: white;
  }

  .seperator-diamond {
    width: 15px;
    height: 15px;
    margin: 5px;
    background-color: rgb(226, 226, 226);
    transform: rotate(45deg);
  }
}

@media only screen and (min-width: 768px) {
  .seperator {
    width: 20vw;
  }
}

@media only screen and (min-width: 1200px) {
  .seperator {
    width: 10vw;
  }
}
